import React, { Component } from "react";
import Navigation from "./navigation";
import thanks from "../thanks1.svg";
import thanks2 from "../thanks2.svg";
class Contribution extends Component {
  state = {};
  render() {
    return (
      <section className="contribution">
       <Navigation />
       <h2 className="contribution_logo">Amawal</h2>
       
       <div className="contribution_form_container">
           <div className="input_container">
           <label htmlFor="mot">Le Mot</label> <br/>
           <input placeholder="Entrer le Mot" type="text" name="mot"/>
           </div>
           <div className="input_container">
           <label htmlFor="mot">L'origine</label> <br/>
           <input placeholder="Entrer L'origine Du Mot" type="text" name="mot"/>
           </div>
           <div className="input_container">
           <label htmlFor="mot">Les Synonymes</label> <br/>
           <input placeholder="Entrer Les Synonymes Du Mot" type="text" name="mot"/>
           </div>
        
           <div className="input_container">
           <label htmlFor="mot">Wilaya</label> <br/>
           <input placeholder="Entrer le mot" type="text" name="mot"/>
           </div>
           <div className="input_container text">
           <label htmlFor="mot">La Definition</label> <br/>
           <textarea placeholder="Entrer La Definition Du Mot" type="text" name="mot"/>
           </div>
           <div className="input_container text left">
          <div className="btn center">CONTRIBUER</div>
           </div>
           <img className="thanks2" src={thanks2} alt=""/>
       <img className="thanks1" src={thanks} alt=""/>
       </div>
       
       
      </section>
    );
  }
}

export default Contribution;
